@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700&display=swap);
body {
  margin:0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* autoprefixer grid: autoplace */

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

#About{padding-top:7%}.about{background-color:#f8ebef}.about>div:first-child{margin-bottom:20px}.about h1{color:#ad6359;font-weight:900;padding:20px 0}.about p{font-size:22px}.about .maxWidth500{max-width:500px}.about .img-fluid{max-width:100%}.about .content{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center}.img-fluidBanner{padding-top:12%;padding-bottom:3%;max-width:100%;height:550px;width:100%;object-fit:cover}

.title{color:#ad6359;font-family:Montserrat;font-size:48px;font-style:normal;font-weight:800;line-height:59px;letter-spacing:0em;text-align:center}

#FAQ{padding-top:10%;padding-bottom:5%}.wrapper{text-align:center;margin:0px auto}.cardWrapper{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:30px 0}.card{background:#f8ebef;border-radius:10px;margin:20px 20px;padding:25px;color:#193d5d;-ms-flex:1 1 26%;flex:1 1 26%}.question{font-family:'Roboto', sans-serif;font-size:24px;font-style:normal;font-weight:500;line-height:28px;letter-spacing:0em;text-align:left}.answer{font-family:'Roboto', sans-serif;font-size:18px;font-style:normal;font-weight:400;line-height:21px;letter-spacing:0em;text-align:left}


.footer{background-color:#193d5d;color:#b3b3b3;padding:2rem 0 1rem 0}.footer .link{color:#b3b3b3;font-weight:700;text-decoration:none}.footer .link:hover{text-decoration:none;color:white}.footer h2{font-weight:800}.footer h2,.footer h3{margin:0}.footer h3{color:white;padding-bottom:10px}@media screen and (max-width: 599px){.footer{text-align:center}h3{padding-top:30px}}

.header{width:100%;background-color:#ffffff !important;color:#ad6359 !important}.logo{display:-ms-flexbox;display:flex;font-family:Work Sans, sans-serif;font-weight:600 !important;color:#193d5d !important;text-align:left}.menuButton{font-family:Work Sans, sans-serif !important;font-weight:700 !important;margin:0 15px !important;text-transform:none !important;color:#ad6359 !important;text-align:right !important}.menuButton:hover{color:#193d5d !important;cursor:pointer}.menuButton:active{color:#193d5d !important;cursor:pointer}.menuButton:focus{color:#193d5d !important;cursor:pointer}.toolbarDiv{width:100% !important;display:-ms-flexbox !important;display:flex !important;-ms-flex-direction:row !important;flex-direction:row !important}.navHeaderBar{width:100% !important;display:-ms-flexbox !important;display:flex !important;-ms-flex-direction:row !important;flex-direction:row !important;-ms-flex-pack:justify;justify-content:space-between}.drawerContainer{padding:20px 30px !important}.active{color:#193d5d !important}.activeTextColor{color:#193d5d;-webkit-text-decoration:#193d5d;text-decoration:#193d5d}.centerRow{-ms-flex-item-align:center;align-self:center}@media screen and (max-width: 900px){.header{padding-left:0 !important}}@media screen and (max-width: 1024px){.menuButton{margin-left:10px !important}.active{color:#193d5d !important}.activeTextColor{color:#193d5d !important;-webkit-text-decoration:#193d5d !important;text-decoration:#193d5d !important}}

#Schedule{padding-top:7%}.button{margin-top:16px !important;padding:2px 12px !important;background-color:#0069ff !important;color:white !important;box-shadow:none !important;text-transform:capitalize !important;font-size:14px;border-radius:6px}.scheduleCardWrapper{display:-ms-flexbox;display:flex;-ms-flex-pack:distribute;justify-content:space-around;margin:20px 0;-ms-flex-wrap:wrap;flex-wrap:wrap}.scheduleCard{background-color:#f3f3f3;border-radius:10px;padding:25px;-ms-flex:1 1;flex:1 1;margin:20px 20px}.scheduleCardContent{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between}.scheduleCardContent img{height:100px;width:100px;border-radius:10px;object-fit:cover}.scheduleTopic{font-size:18px;font-style:normal;font-weight:900;line-height:28px;letter-spacing:0em;text-align:left;max-width:200px;margin:0 25px 10px 0;display:-webkit-box;-webkit-line-clamp:3;-webkit-box-orient:vertical;overflow:hidden}.scheduleName{font-size:16px;font-style:normal;font-weight:500;line-height:21px;letter-spacing:0em;text-align:left}.scheduleTime{font-size:16px;font-style:normal;font-weight:500;line-height:21px;letter-spacing:0em;text-align:left;color:#ad6359;margin:5px 0}

#Team{padding:5% 0}.wrapper{text-align:center;margin:0px auto}.teamWrapper{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;margin:50px 0}.avatar{-ms-flex:1 1;flex:1 1;margin:20px 20px}.avatar img{border-radius:50%;height:180px;transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s}.avatar img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.avatar .name{font-weight:bold;margin-top:15px;margin-bottom:15px;color:inherit}

#Sponsor{padding-top:10%}.sponsors{margin:0px auto}.sponsors h1{color:#ad6359;font-weight:900;padding:20px 0}.sponsors .img-fluid{max-width:100%;height:80px;object-fit:contain;padding:20px 0}.sponsors p{font-size:22px}.sponsors .maxWidth{max-width:250px}.sponsorLogos{margin:30px auto}

title-text{font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif}#Speakers{padding-top:7%}.intro{text-align:center}.team-boxed{color:#313437}.team-boxed .intro{font-size:16px;max-width:500px;margin:0 auto}.team-boxed .box{text-align:center;padding:15px 0px 15px 0px;background-color:#fff;margin-bottom:30px;border-radius:10px}.team-boxed .item .name{font-weight:bold;margin-top:15px;margin-bottom:0px;color:inherit}.team-boxed .item .role{text-transform:uppercase;font-weight:bold;color:#6d6c6c;letter-spacing:2px;font-size:13px}.team-boxed .item .job{text-transform:uppercase;font-weight:bold;color:#6d6c6c;margin-top:2px;font-size:11px}.team-boxed .item img{max-width:160px;transition:-webkit-transform 0.2s;transition:transform 0.2s;transition:transform 0.2s, -webkit-transform 0.2s;margin:0 auto;cursor:default;width:9em}.team-boxed .item img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.team-boxed .social{font-size:12px;color:#a2a8ae}.team-boxed .social:hover{font-size:12px;color:#ad6359}.team-boxed .social a{color:inherit;margin:0 8px;display:inline-block;opacity:0.6;padding:0px}.team-boxed .social a:hover{opacity:1}.rounded-circle{border-radius:50%}

